import React, { useEffect, useState } from 'react';
import { getCoin } from '../../../api/Coin';
import '../../../App.css';
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Card from 'react-bootstrap/Card';
import './Services.css';
import { Link } from 'react-router-dom';
import Carousel from 'react-bootstrap/Carousel';
import Loader from 'react-loader-spinner';
import { getProfile } from '../../../api/Profile';
import { MdLocationOn } from "react-icons/md";

const Services = (props) => {
    const [loading, setLoading] = useState(true);
    const [doc, setDoc] = useState({});
    const [show, setShow] = useState([]);
    useEffect(async () => {
        let temp = await getCoin();
        setDoc(temp);
        setShow(temp);
        setLoading(false);
    }, [])
    let Service = <div></div>;

    const objectMap = (obj, fn) =>
        Object.fromEntries(
            Object.entries(obj).map(
                ([k, v], i) => [k, fn(v, k, i)]
            )
        )
    const onChangeHandler = (e) => {
        const req = e.target.value.toLowerCase();
        const filters = req.split(',');
        console.log(filters);
        setShow(doc.filter((d) => {
            for (const fi in filters) {
                const f = filters[fi].trim();
                if (f.length == 0) continue;
                if (!((d.title).toLowerCase().includes(f) || (d.publisher).toLowerCase().includes(f) || (d.location && (d.location).toLowerCase().includes(f)))) {
                    return false;
                }
            }
            return true;
        }
        ));
    }
    const dp = [];

    objectMap(show, function (c) {
        const carous = [];
        for (let i = 0; i < c.image.length; i++) {
            carous.push(
                <Carousel.Item>
                    <img
                        className="wid coin--img"
                        src={c.image[i]}
                        alt="First slide"
                    />
                </Carousel.Item>);
        }

        if (c.image.length == 1)
            dp.push(< img className="coin--img" src={c.image[0]} />);
        else
            dp.push(c.image[0] ? <Carousel fade className="size">{carous}</Carousel> : <img className="coin--img" src="data:image/jpeg;base64," />)
    })


    let i = 0;
    Service = loading == true ?
        <div className="wrappp"><Loader
            type="Oval"
            color="rgb(4,21,59)"
            height={150}
            width={150}
        /></div>
        :
        <div>
            <div className="search--btn">
                <input id='search-btn' type='checkbox' />
                <label for='search-btn'>Show search bar</label>
                <input id='search-bar' onChange={(e) => onChangeHandler(e)} type='text' placeholder='Search...' />
            </div>

            <div className='new coin--container'>
                <Row className="wee" xs={1} lg={2} xl={3} >
                    {show.map((coin) => (
                        <Col className='coin--item'>
                            <Card style={{minWidth:"300px"}} className="new-c">
                                <div className="coin--wrapper">
                                    {dp[i++]}
                                </div>
                                <Link to={{ pathname: "/getCoin/" + coin.publisher + "/" + coin._id, param1: coin, param2: props.username }}>
                                    <Card.Body>
                                        <Card.Title className="new--text">{coin.title}</Card.Title>
                                    </Card.Body>
                                </Link>
                                <Card.Footer>
                                    <Link to={"/view/" + coin.publisher}><small className="text-muted"><b>Published by: </b>{coin.publisher}</small></Link>
                                    <span style={{ float: "right" }}>
                                        <MdLocationOn />
                                        <span style={{ marginLeft: "10px" }}>{coin.location ? coin.location : "Undefined"}</span>
                                    </span>
                                </Card.Footer>
                            </Card>
                        </Col>
                    ))}
                </Row>
            </div>
        </div>

    return (
        <div>
            {Service}
        </div>
    )
}



export default Services;
