import React, { useState } from 'react';
import axios from 'axios';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import { addCoin } from '../../api/Coin';
import Carousel from 'react-bootstrap/Carousel'
import './AddCoin.css';
import {getProfile} from '../../api/Profile';
import {withRouter} from 'react-router';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';


toast.configure();





function AddCoin(props) {

    const [file, setFile] = useState(null);
    const [preview, setPreview] = useState(null);
    const [image, setImage] = useState([]);
    const [title, setTitle] = useState("");
    const [description, setDescription] = useState("");


    const fileUploadHandler = async (file) => {
        const formData = new FormData();
        formData.append("file", file);
        formData.append("upload_preset", "mhabs7f9");

        return await axios.post("https://api.cloudinary.com/v1_1/dx0rf8u0t/image/upload", formData).then(async (res) => {
            // setImage([
            //     ...image,
            //     res.data.secure_url]);

            return res.data.secure_url;
        })
    }

    // console.log(image);

    const fileSelectedHandler = async (e) => {
        const files = e.target.files;
        const images = [];
        for (let i = 0; i < files.length; i++) {
            const t = await fileUploadHandler(files[i]);
            images.push(t);
        }
        setImage(images);

    }

    const onSubmitHandler = async () => {
        const data = {};
        data["publisher"] = props.username;
        data["title"] = title;
        data["image"] = image;
        data["description"] = description;
        const profile=await getProfile(props.username);
        data["location"]=profile.location;
        const t = await addCoin(props.username, data,profile);
        toast.success("Coin Added Successfully!");
        props.history.push('/');
    }

    const carous = [];

    for (let i = 0; i < image.length; i++) {
        carous.push(
            <Carousel.Item>
                <img
                    className="d-block w-100 img"
                    src={image[i]}
                    alt="First slide"
                />
            </Carousel.Item>);
    }

    const dp = [];
    if (image.length == 1)
        dp.push(< img className="img" src={image[0]} />);
    else
        dp.push(image[0] ? <Carousel fade className="size2">{carous}</Carousel> : <img className="img" src="data:image/jpeg;base64," />)


    return (
        <>
            <div className="form-container">
                <Form className="form-wrapper shadow">
                    <div className="align">
                        {dp}
                    </div>
                    <Form.Group controlId="formFileMultiple" className="mb-3">
                        <Form.Label><strong>Coin Image</strong></Form.Label>
                        <Form.Control type="file" onChange={fileSelectedHandler} multiple />
                    </Form.Group>

                    <Form.Group className="mb-3" controlId="formBasicEmail">
                        <Form.Label><strong>Title</strong></Form.Label>
                        <Form.Control type="text" placeholder="Enter Title" onChange={(e) => setTitle(e.target.value)} />
                    </Form.Group>

                    <Form.Group className="mb-3" controlId="formBasicPassword">
                        <Form.Label><strong>Description</strong></Form.Label>
                        <Form.Control type="text" placeholder="description" onChange={(e) => setDescription(e.target.value)} />
                    </Form.Group>
                    <div className="d-grid gap-2">
                        <Button variant="success" size="sm" className="mar" type="submit" onClick={(e) => {
                            e.preventDefault();
                            onSubmitHandler();
                        }}>
                            Submit
                        </Button>
                    </div>
                </Form>
            </div>
        </>
    )
}


export default withRouter(AddCoin);