import "./Conversation.css";

const Conversation = ({ conversation, currentUser }) => {
    let friendImg="data:image/png;base64,";
    let friendName="John";

    for(const k in conversation.images)
    {
        if(k!=currentUser.username)
        {
            friendImg=conversation.images[k];
            friendName=k;
            break;
        }
    }
    let hasRead=true;
    for(const i in conversation.read)
    {
        if(conversation.read[i]==false && i==currentUser.username) hasRead=false;
    }
    let toRender = <div></div>
    let showNotRead=<span></span>
    if(!hasRead)
    {
        showNotRead=<span className="newMessage">*</span>
    }
    if (true) {
        toRender = <div className="conversation">
            <img className="conversationImg"
                src={friendImg}
                alt={friendName} />
            <span className="conversationName">{friendName}</span>
            {showNotRead}
        </div>
    }
    return (
        <>
            {toRender}
        </>
    )
}
export default Conversation;